<template>
  <div id="contentWrapper">
    <h1>{{title}}</h1>
    <div v-for="(methodObj,index) in arr" :key="index">
      <div v-if="Object.values(methodObj)[0].length > 0">
        <hr />
        <p><h3>{{Object.keys(methodObj)[0]}}:</h3></p>
      </div>
      <div v-for="(textrow,index) in Object.values(methodObj)[0]" :key="index">
        <b>{{Object.keys(textrow)[0]}}:</b>
        <p v-for="(textRow,index) in Object.values(textrow)[0]" :key="index">{{textRow}}</p>
      </div>
    </div>    
  </div>
</template>

<script>
import {filesArr} from "../arrHolder/filesArr.js";

export default {
  name: "CurrentFileContent",
  props: {
    fileName: String,
  },
  data() {
    return {
      title: null,
      arr: null,
      filesArr,
    }
  },
  watch: {
    fileName() {
      this.filesArr.find(obj => {
        const key = Object.keys(obj)[0]
        if(key === this.fileName) {
          this.title = key
          return this.arr = obj[key]
        }
      })
    }
  },
};
</script>

<style scoped lang="scss">
#contentWrapper {
  width: 100%
}
</style>
